
import React from "react";
import { Outlet, RouteObject, useRoutes } from "react-router-dom";
import UserPage1 from "../components/UserPage1";
import UserPage2 from "../components/UserPage2";
import UserPage3 from "../components/UserPage3";

import { isLoginIn } from "./MyFirebaseLocal";

const MyFirebaseSignInButton = React.lazy(() => import('./MyFirebaseAuth'));


export function AppRouters() {

    const routers: RouteObject[] = [

        {
            path: '/',
            element: !isLoginIn() ? <MyFirebaseSignInButton />
                :<><Outlet></Outlet></>
                , 
            children: [
                { path: "", element: <UserPage1 nextpage={"UserPage2"} /> }, 
                { path: "UserPage1", element: <UserPage1 nextpage={"UserPage2"} /> }, 
                { path: "UserPage2", element: <UserPage2 nextpage={"UserPage1"} returnpage={"UserPage1"} /> }, 
                { path: "UserPage3", element: <UserPage3 nextpage={"UserPage1"} /> }, 
                { path: "Signout", element: <MyFirebaseSignInButton />},
            ],

        }
    ]

    return useRoutes(routers)
}