
import { Button, Col, message, Row } from "antd";
import { useNavigate } from 'react-router-dom';

import React, { useState } from "react";

import styles from "./button1a.module.css"

const mybutton2 = {
  minWidth: "100px",
  minHeight: "60px",
  fontSize: '200%'
};

type Prop = {
  nextpage: string;
};

export default function UserPage1(prop: Prop) {
  const navigate = useNavigate()



  const [sId, setSId] = useState<string>("");

  function clickSId(value: number) {
  
    if (sId.length < 3) {
      setSId(sId + value)
    }
  }

  function Square(value: string){
    return (
      <button
        value={value}
        className={styles.button1}
        onClick={()=> clickSId(Number(value))}
      >
        {value}
      </button>
    );
  };


  function detal() {
    const now = new Date()

    return (now.getHours() <= 12 ? "" : "") + " XXX "
  }

  return (
    <>
    
      <Row justify="center" align="top">
        <Col>
          <h1>: {sId}</h1>
        </Col>
      </Row>
      <Row justify="center" align="top">
        <Col >
          <h2>{detal()}</h2>
        </Col>
      </Row>

      <div style={{ height: "100%" }}>


        <Row align="top" justify="center">
        {['1', '2', '3'].map((value, index, array) => {
            return (
              <Col key={value}>
                { Square(value)}
              </Col>
            );
          })}
        </Row>
        <Row align="top" justify="center">
        {['4', '5', '6'].map((value, index, array) => {
            return (
              <Col key={value}>
                { Square(value)}
              </Col>
            );
          })}
        </Row>
        <Row align="top" justify="center">
        {['7', '8', '9'].map((value, index, array) => {
            return (
              <Col key={value}>
                { Square(value)}
              </Col>
            );
          })}
        </Row>
        <Row align="top" justify="center">
        {['0'].map((value, index, array) => {
            return (
              <Col key={value}>
                { Square(value)}
              </Col>
            );
          })}
        </Row>




      </div>
      <br />
      <Row align="top" justify="center" gutter={[24, 24]}>
        <Col key={""}>

          <Button type="primary" style={mybutton2} onClick={() => {
            if (sId.length <= 0) {
              message.error("")
              return
            }
            if (sId.length > 3) {
              message.error("")
              return
            }



            navigate(`/${prop.nextpage}?id=${sId}`)
          }} >
            
          </Button>
        </Col>
        <Col key={""} >
          <Button style={mybutton2} type="dashed" onClick={() => setSId("")}>
            
          </Button>
        </Col>
      </Row>
    </>
  );
}
